import React from "react";
import style from "./Nav.module.css"

export const Nav = () => {
    return (
        <div id={"nav"} className={style.nav}>
            <a href={"#main"}>Main</a>
            <a href={"#skills"}>Skills</a>
            <a href={"#projects"}>Projects</a>
            <a href={"#contacts"}>Contacts</a>
        </div>
    )
}